import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col, Form } from 'antd';
import { getGradingStatistics } from 'services/classCreditSchedule';

import { TableData } from 'components/common';
// import { insertScoreBag } from 'dump/data';
import FormSeacrh from './FormSeacrh';

@Form.create({ name: 'formStatisticEntryPoints' })
class PageStatisticEntryPoints extends Component {
  state = {
    paramSearch: {},
  };

  componentDidMount() {}

  dataForm = value => {
    this.setState({
      paramSearch: value,
    });
    this.getDataTable(value);
  };

  render() {
    const columns = [
      {
        title: 'Lớp tc',
        dataIndex: 'syllableCode',
        key: 'syllableCode',
      },
      {
        title: 'Mã học phần',
        dataIndex: 'subjectId',
        key: 'subjectId',
      },
      {
        title: 'Tên học phần',
        dataIndex: 'subject',
        key: 'subject',
      },
      {
        title: 'Giáo viên',
        dataIndex: 'teacherName',
        key: 'teacherName',
      },
      {
        title: 'Số sv đã nhập điểm',
        dataIndex: 'inputNo',
        key: 'inputNo',
      },
      {
        title: 'Trạng thái nhập điểm',
        dataIndex: 'status',
        key: 'status',
      },
    ];
    return (
      <Card title="Nhập điểm theo túi thi">
        <Row>
          <Col span={24}>
            <FormSeacrh dataForm={this.dataForm} />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              reload={this.state.reload}
              paramSearch={this.state.paramSearch}
              service={getGradingStatistics}
              columns={columns}
              // allowGetData={!!trainTypeId && !!facultyId && !!courseId}
              // loading={loading}
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <PageStatisticEntryPoints />
  </Page>
);



