import React, { Component } from 'react';
import { Col, Form, Row } from 'antd';
import { BtnSearch, BtnClear } from 'components/common/button';
import FilterPathEducate from 'components/common/input/FilterPathEducate/index';
import YearSemesterInput from 'components/common/input/YearSemesterInput';
import { SelectAjax } from 'components/common';
import { getTeacherInClassCredit } from 'services/classCreditSchedule';
import { getListSubject } from 'services/Subject';
import { updateQueryParams, getQueryParams } from 'utils/urlUtil';

@Form.create({ name: 'formTeacher' })
class FormSearch extends Component {
  state = {};
  componentDidMount = () => {
    const { form } = this.props;
    form.setFieldsValue(getQueryParams());
  };

  handleReset = () => {
    this.props.form.resetFields();
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) return;
      this.props.dataForm(values);
      updateQueryParams(values);
    });
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    const optionFieldDecorator = {
      trainTypeId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Hệ!',
          },
        ],
      },
      facultyId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khoa!',
          },
        ],
      },
      yearEduId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Năm học',
          },
        ],
      },
      semesterId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Học kỳ',
          },
        ],
      },
    };

    const { trainTypeId, facultyId, yearEduId, semesterId } = this.props.form.getFieldsValue();

    return (
      <Form layout="vertical" className="form-inline-label" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate
            classes
            form={this.props.form}
            optionFieldDecorator={optionFieldDecorator}
            column={6}
          />
          <YearSemesterInput
            form={this.props.form}
            optionFieldDecorator={optionFieldDecorator}
            span={6}
          />
          <Col span={6}>
            <Form.Item label="Học phần">
              {getFieldDecorator(
                'subjectId',
                optionFieldDecorator.subjectId
              )(
                <SelectAjax
                  // setFieldsValue={setFieldsValue}
                  service={getListSubject}
                  // params={{ trainTypeId }}
                  // allowGetData={!!trainTypeId}
                  // open={!!trainTypeId && !facultyId}
                />
              )}
            </Form.Item>
          </Col>

          <Col span={6}>
            <Form.Item label="Giáo viên">
              {getFieldDecorator(
                'teacherId',
                optionFieldDecorator.teacherId
              )(
                <SelectAjax
                  keyLabel="label"
                  keyValue="value"
                  service={getTeacherInClassCredit}
                  params={{ trainTypeId, facultyId, yearEduId, semesterId }}
                  allowGetData={!!trainTypeId && !!facultyId && yearEduId && semesterId}
                />
              )}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={24} style={{ textAlign: 'right' }}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={() => this.handleReset()} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default FormSearch;



